<template>
    <div v-show="is_not_main" style="background-color: #fff; box-shadow: 0px 0px 0px 0 rgba(255,255,255)">
        <nav class="light">
            <header class="cd-header" style="background-color: #f3f7ff">
                <span
                    style="position: fixed;padding-top: 13px;right: 7%;font-size: 20px;
                    font-family: 'Roboto Slab',serif;font-weight: bold; color: #273f8a">自动驾驶安全测试平台</span>
                    <div id="crumbs">
                        <br />
                        <br />
                <ul class="visible">
                    <li v-for="(crumb, index) in crumbs" :key='index' ><a :href="crumb.name"> {{ crumb.crubItem }} </a></li>
                </ul>
            </div>
                </header>
            <div class="nav">
                <div class="nav-but-wrap header-wrapper ">
                    <div class="menu-icon hover-target">
                        <span class="menu-icon__line menu-icon__line-left" ></span>
                        <span class="menu-icon__line" ></span>
                        <span class="menu-icon__line menu-icon__line-right"></span>
                    </div>
                </div>
                <div class="nav__content">
                    <div class="right"></div>
                    <ul class="nav__list">
                        <li :class="{ 'nav__list-item': true, 'active-nav': isActive1 }"><a href="/index"
                                class="hover-target">主页</a></li>
                        <li :class="{ 'nav__list-item': true, 'active-nav': isActive2 }"><a href="/library"
                                class="hover-target">场景库</a></li>
                        <li :class="{ 'nav__list-item': true, 'active-nav': isActive3 }"><a href="/test"
                                class="hover-target">仿真测试</a></li>
                        <li :class="{ 'nav__list-item': true, 'active-nav': isActive4 }"><a href="/accidentAnalysis"
                                class="hover-target">事故分析</a></li>
                    </ul>
                </div>
            </div>

        </nav>
<br />
<br />
<br />
</div></template>
<script>
export default {
    name: 'NavMenu',
    data() {
        return {
            crumbs: [
            {name: '', crubItem: ''}
            ],
            keywords: '',
            isActive1: false,
            isActive2: false,
            isActive3: false,
            isActive4: false,
            is_not_main: true
        }
    },

    computed: {
        hoverBackground() {
            return '#f3f7ff'
        }
    },
    mounted() {
        var frame = this
        var path1 = frame.currentPath()
        if (path1 === '/index') {
            this.isActive1 = true
            this.is_not_main = false
        }
        if (path1 === '/library') {
            this.isActive2 = true
        }
        if (path1 === '/test') {
            this.isActive3 = true
        }
        if (path1 === '/accidentIndex') {
            this.isActive3 = true
        }
        if (path1 === '/accidentAnalysis') {
            this.isActive4 = true
        }

        var newVal = frame.$route.path
        var newitem = newVal.split('/')[newVal.split('/').length - 1]
        var querys = frame.$route.query
        var url_query = ''
        Object.keys(querys).forEach(key => {
            url_query += key + '=' + querys[key] + '&'
        })
        url_query = url_query.substring(0, url_query.length - 1)

        if (newitem === 'testResult') {
            this.crumbs = [
            {name: '/test', crubItem: 'test'},
            {name: '/test/testResult?' + url_query, crubItem: 'testResult'}]
        }
        if (newitem === 'accidentIndex') {
            this.crumbs = [
            {name: '/test', crubItem: 'test'},
            {name: '/test/testResult?' + url_query, crubItem: 'testResult'},
            {name: '/accidentIndex?' + url_query, crubItem: 'accidentIndex'}]
        }
        if (newitem === 'details') {
            this.crumbs = [
            {name: '/test', crubItem: 'test'},
            {name: '/test/testResult?' + url_query, crubItem: 'testResult'},
            {name: '/accidentIndex?' + url_query, crubItem: 'accidentIndex'},
            {name: '/accident/details?' + url_query, crubItem: 'details'}]
        }
        var myapp = function () {
            var nav2 = document.querySelectorAll('nav')[0]
            var menu = document.querySelector('.menu-icon')
            var right = document.querySelector('.right')
            if (nav2.classList.contains('nav-active')) nav2.classList.remove('nav-active')
            var applyListeners = function applyListeners() {
                menu.addEventListener('click', function () {
                    console.log('click')
                    return toggleClass(nav2, 'nav-active')
                })
                right.addEventListener('click', function () {
                    console.log('click')
                    return toggleClass(nav2, 'nav-active')
                })
            }
            var toggleClass = function toggleClass(element, stringClass) {
                if (element.classList.contains(stringClass)) element.classList.remove(stringClass); else element.classList.add(stringClass)
            }
            applyListeners()
            console.log('add')
        }
        myapp()
    },
    watch: {
        '$route.path': function(newVal, oldVal) {
                        console.log(`new_path = ${newVal}, old_path = ${oldVal}`)
                        var querys = this.$route.query
                        var url_query = ''
                        Object.keys(querys).forEach(key => {
                        url_query += key + '=' + querys[key] + '&'
                    })
                    url_query = url_query.substring(0, url_query.length - 1)
                    console.log(url_query)
                    var newitem = newVal.split('/')[newVal.split('/').length - 1]
                    if (this.crumbs.length === 1 && this.crumbs[0].crubItem === '') {
                        this.crumbs = [{name: oldVal, crubItem: oldVal.substring(1, oldVal.length)}]
                    }

                    var is_new = true
                    for (let key in this.crumbs) {
                        console.log(this.crumbs[key].crubItem)
                        console.log(newitem)
                        console.log(this.crumbs[key].crubItem === newitem)
                        if (this.crumbs[key].crubItem === newitem) {
                            this.crumbs.pop()
                            is_new = false
                        }
                    }
                    if (is_new) {
                        var new_url = newVal + '?' + url_query
                       this.crumbs.push({name: new_url, crubItem: newitem})
                    }
                    if (this.crumbs.length === 1) {
                        this.crumbs = [{name: '', crubItem: ''}]
                    }
                }
    },
    methods: {
        currentPath() {
            var x = this.$route.path.indexOf('/', 1)
            console.log(this.$route.path)
            console.log(this.$route.query)
            if (x !== -1) {
                return this.$route.path.substring(0, x)
            } else {
                return this.$route.path
            }
        }
    }
}
</script>

<style scoped>
a {
    text-decoration: none;
}
span {
    pointer-events: none;
}
a:hover {
    text-decoration: none;
}
