    font-family: 'Roboto Slab',serif;
    font-weight: 600;
    padding-left: 30px;
    padding-top: 5px;
    padding-bottom: 5px;
    display: inline-block;
    -webkit-transition: all 200ms linear;
    transition: all 200ms linear;
}
.nav__list-item a:after {
    z-index: 1;
    position: absolute;
    content: '';
    top: 50%;
    left: 10px;
    width: 5px;
    height: 0;
    opacity: 0;
    background-color: #3779e5;
    -webkit-transition: all 200ms linear;
    transition: all 200ms linear;
}
.nav__list-item a:hover:after {
    height: 100%;
    opacity: 1;
    top: 0;
