nav.nav-active .menu-icon__line-right {
    width: 15px;
    float: right;
    -webkit-transform: translate(-3px, -3.5px) rotate(45deg);
    transform: translate(-3px, -3.5px) rotate(45deg);
}
nav.nav-active .menu-icon:hover .menu-icon__line-left,
nav.nav-active .menu-icon:hover .menu-icon__line-right {
    width: 15px;
}
nav.nav-active .nav {
    visibility: visible;
}
nav.nav-active .nav:before,
nav.nav-active .nav:after {
    width: 250px;
    height: 800px;
    border-radius: 0px;
}
nav.nav-active .right:after {
    width: 100%;
    height: 100%;
    z-index: -1;
    left: 250px;
    border-radius: 0px;
    background: #fff;
    opacity: 0;
}
nav.nav-active .nav:after {
    -webkit-transition-delay: .1s;
    transition-delay: .1s;
