<template>
  <div>
    <div class="container" v-show="!visibleFlag">
      <div class="top">
        <div class="text">
          <span class="letter" v-for="(char, index) in message" :key="index">{{ char }}</span>
        </div>
      </div>
      <div class="bottom">
        <br />
        <br />
        <button v-if="visibleChars === message.length" class="button" @click="showPages">Enter</button>
      </div>
    </div>
    <div class="pages" v-show="visibleFlag">
      <router-view v-show="visibleFlag"/>
    </div>
  </div>
</template>

<script>
import NavMenu from './components/common/NavMenu.vue'
export default {
  name: 'App',
  components: {NavMenu},
  data () {
    return {
      message: '自动驾驶安全测试平台',
      visibleFlag: false,
      visibleChars: 0
    }
  },
  mounted () {
    let flag = 'false'
    // 获取 localStorage，检查过期时间
    const now = new Date().getTime()
    const storedExpiration = localStorage.getItem('expiration')
    if (now > storedExpiration) {
      // localStorage 已过期，删除相应的 key
      localStorage.removeItem('visibleFlag')
    } else {
      // localStorage 未过期，可以正常获取 value
      flag = localStorage.getItem('visibleFlag')
    }
    if (flag === 'true') {
      this.visibleFlag = true
    } else {
      this.showText()
    }
  },
  methods: {
    showText () {
      let letters = document.querySelectorAll('.letter')
      letters.forEach((letter, index) => {
        setTimeout(() => {
          letter.style.opacity = '1'
          letter.style.transform = 'translateX(0)'
          this.visibleChars += 1
        }, 50 * index)
      })
    },
    showPages () {
      // 设置 localStorage，过期时间为半个小时
      const expiration = new Date().getTime() + (30 * 60 * 1000)
      localStorage.setItem('visibleFlag', 'true')
      localStorage.setItem('expiration', expiration)
      this.visibleFlag = true
    }
  }
}
</script>

<style scoped>
.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  /*background-color: #e6e6e6;*/
  /*background-color: #4876FF;*/
}

.text {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.letter {
  opacity: 0;
  transform: translateX(-20px);
  font-size: 80px;
  font-weight: bold;
  color: #333;
  transition: opacity 1s ease-in-out, transform 1s ease-in-out;
}

.button {
  opacity: 0;
  border: none;
  font-size: 20px;
  background-color: transparent;
  animation-name: floatUp;
  animation-duration: 1s;
  animation-delay: 1s;
  animation-fill-mode: forwards;
  /*background-color: #5daf34;*/
}

@keyframes floatUp {
  from {
    opacity: 0;
    transform: translateY(50px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin: 0 auto;
    background-image: url(https://user-images.githubusercontent.com/107924376/221787434-9c641c0d-1567-46c7-a40c-cd43c5b941b3.PNG);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-attachment: fixed;
    /* height: 100%; */
    /*margin-top: 10px;*/
  }
  *{
    margin:0;
    padding:0;
    border:0;
    box-sizing: border-box;
  }
  body{
    margin: 0 auto !important;
  }

</style>



