  <div>
    <nav-menu class="nav-menu"></nav-menu>
    <router-view/>
  </div>
</template>
<script>

  import NavMenu from '@/components/common/NavMenu'
  export default {
    components: {NavMenu}
  }
</script>

<style>
  .nav-menu {
    /*margin-bottom: 40px;*/
    box-shadow: 0 6px 6px 0 rgba(0,0,0,.05);
  }
  *{
    margin:0;
    padding:0;
    border:0;
    box-sizing: border-box;
  }
  body{
    margin: 0 auto !important;
  }
</style>



